.summary {
  display: flex;
  flex-direction: row;
  width: 100%;
  height: 125px;
}
.summary button {
  text-decoration: none;
  border: none;
  background-color: steelgray;
  margin: 40px 5px;
  height: 20px;
  width: 20px;
}
.summary div img {
  height: 100px;
  width: 100px;
}
.quantity {
  display: inline-flex;
  position: absolute;
  right: 0;
  line-height: 100px;
  vertical-align: middle;
}
.totalPerProduct {
  position: absolute;
  margin-top: 20%;
  margin-left: 62%;
}
button.removeAllQuantities {
  position: absolute;
  margin-top: 1%;
  margin-left: 94%;
}
